import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from "@angular/router"
@Component({
  selector: 'app-how-it-works-detail',
  templateUrl: './how-it-works-detail.component.html',
  styleUrls: ['./how-it-works-detail.component.css']
})
export class HowItWorksDetailComponent implements OnInit {

  id: number;
  question:any;

  constructor(private route: ActivatedRoute) { }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.id = params.id;
      this.question = this.route.snapshot.data.questions[this.id]
    })


  }

}



