import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';

import { AppComponent } from './app.component';
import { MenuComponent } from './menu/menu.component';
import { MenuCardComponent } from './menu/menu-card/menu-card.component';
import { HowItWorksComponent } from './how-it-works/how-it-works.component';
import { MessagesModule } from "./messages/messages.module";
import { HowItWorksDetailComponent } from './how-it-works-detail/how-it-works-detail.component';
import { FaqResolverService } from "./resolvers/faq-resolver.service"
import { DataService } from "./services/data.service"

@NgModule({
  declarations: [
    AppComponent,
    MenuComponent,
    MenuCardComponent,
    HowItWorksComponent,
    HowItWorksDetailComponent,
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    MessagesModule,
    RouterModule.forRoot([
      {
        path: 'menu',
        component: MenuComponent
      },
      {
        path: "",
        redirectTo: "menu",
        pathMatch: "full"
      },
      {
        path: 'how/:id',
        component: HowItWorksDetailComponent,
        pathMatch:'full',
        resolve:{
          questions:FaqResolverService
        }
      },
      {
        path: 'how',
        component: HowItWorksComponent
      },
      {
        path: 'settings',
        loadChildren: './settings/settings.module#SettingsModule'
      }
    ])
  ],
  providers: [FaqResolverService, DataService],
  bootstrap: [AppComponent]
})
export class AppModule { }



